@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.container {
    padding: 64px;
}
.row-x{
    margin-top: 7%;
}
.column {
    float: left;
    width: 33.3%;
    margin-bottom: 16px;
    padding: 0 8px;
    margin-top: 6%;
}

.card {
    margin: 8px;
}


.card-container {
    padding: 0 16px;
}

.card-container::after,
.row::after {
    content: "";
    clear: both;
    display: table;
}

  
.row:after {
    content: "";
    display: table;
    clear: both
}
  
.column-66 {
    float: left;
    width: 66.66666%;
    padding: 20px;
}
  
.column-33 {
    float: left;
    width: 33.33333%;
    padding: 20px;
}
  
.large-font {
    font-size: 49px;
    color: black;
    margin-top: 13%;
}
  
  
.description-x{
    text-align: justify;
    margin-top: 5%;
    margin-left: 0%;
    margin-right: 9%;
    font-weight: 300;
}

.subdescription{
    margin-top: 5%;
    text-align: justify;
    font-weight: 300;
}

.large-font-partion{
    font-size: 49px;
    color: black;
    margin-top: 4%;

}
.description-partion{
    text-align: justify;
    margin-top: 5%;
    margin-left: 20%;
    margin-right: 20%;
    font-weight: 300;
}

img {
   /*  display: block;
    height: auto;
    max-width: 100%; */
   /*  max-width: 200%;
    margin-left: -30%; */
    margin-left: -30%;
    width: 100%;
}

.social-container {
    padding: 25px 50px;
}
.titule-follow {
    padding: 7px 0px 19px 0px;
}

a.social {
    margin: 0 0.4rem;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms;
    display: inline-block;
}
  
a.social:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
  
a.medium {
    color: black;
}
  
a.telegram {
    color: #4968ad;
}
  
a.twitter {
    color: #49a1eb;
}
  
a.instagram {
    background: linear-gradient(#6616fc,#f394d3);
    color: red;
    background-image: linear-gradient(to bottom, transparent, rgba(0,0,0,.1));
}

@media screen and (max-width: 900px) {
    .container {
        padding: 4px;
    }

    .column-66,
    .column-33 {
      width: 100%;
      text-align: center;
    }
    .large-font {
        font-size: 38px;
        color: black;
        margin-top: 43%;
    }

    img {
        margin: auto;
        width: 60%;
        margin-left: 0px;
    }
    .large-font-partion {
        font-size: 34px;
        color: black;
        margin-top: 33%;
    }

    .description-x {
        text-align: justify;
        margin-top: 10%;
        margin-left: 1%;
    }
    .description-partion {
        text-align: justify;
        margin-top: 10%;
        margin-left: 7%;
        margin-right: 7%;
        font-weight: 300;
    }
    .social-container {
        padding: 0;
        margin-top: 17%;
    }
   

}

@media screen and (max-width: 650px) {
    .column {
        width: 100%;
        display: block;
    }
}

/* ----------- iPad Pro ----------- */
@media only screen and (min-width: 1024px)  and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {

    .column {
        float: left;
        width: 49%;
        margin-bottom: 16px;
        padding: 0 8px;
        margin-top: 6%;
    }
    .App-logoUnicornio{
        margin-left: 80%;
    }
    
}

.header {
    display: -webkit-flex;
    display: flex;
    background: linear-gradient(#6616fc, #f394d3);
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0px 40px;
    height: 80px;
}

.logo-nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.logo-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.logo {
    width: 45px;
    height: 45px;
}

.nav-options {
    padding-left: 25px;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 50px;
    list-style-type: none;
}

.mobile-option {
    display: none;
}

.option :hover {
    color: white;
}

.mobile-menu {
    display: none;
}

@media (max-width: 648px) {
    /*Mobile View */
    .header {
        padding: 0px 10px;
    }
    .logo {
        width: 45px;
        height: 45px;
    }
    .nav-options {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 180px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        -webkit-flex-direction: column;
                flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }
    .nav-options.active {
        background: #9899d1;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        -webkit-align-content: center;
                align-content: center;
        padding-left: 0px;
    }
    .menu-icon {
        width: 45px;
        height: 45px;
    }
    .option {
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }
    
}

.mobile-menu {
    display: block;
}
.app-footer {
    /* position: fixed; */
    left: 0;
    bottom: 0;
    width: 100%;
    background: linear-gradient(#6616fc, #f394d3);
    /* color: white; */
    text-align: center;
    height: 8%;
    color: white;
    padding: 83px 0;
    margin-top: 10%;
   
}

.app-footer__message {

    font-family: sans-serif;
}
.App {
  font-family: Montserrat;
  font-weight: 600;
  text-align: center;
}

a {
  text-decoration: none;
  color: black;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

